export const state = () => ({
  page: null
})

export const mutations = {
  SET_PAGE: (state, payload) => {
    state.page = payload
  }
}

export const actions = {
  // get page
  fetchPage: async ({ commit }, { axios, pageSlug }) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'page', value: true },
        { root: true }
      )
      const response = await axios.get('/api/pages/' + pageSlug)
      commit('SET_PAGE', response.data.page)
      commit(
        'loading/SET_LOADING',
        { option: 'page', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'page', value: false },
        { root: true }
      )
      commit('SET_PAGE', null)
    }
  }
}
