export const state = () => ({
  icons: []
})

export const getters = {
  getIcon: (state) => (slug) => {
    const icon = state.icons.find((i) => i.slug === slug)
    return icon ? icon.icon : ''
  }
}

export const mutations = {
  SET_ICONS: (state, icons) => {
    state.icons = icons
  }
}

export const actions = {
  // fetch all icons
  fetchIcons: async ({ commit }, { axios }) => {
    try {
      const response = await axios.get('/api/icons')
      commit('SET_ICONS', response.data.icons)
    } catch (error) {}
  }
}
