export const state = () => ({
  users: [],
  user: null
})

export const getters = {
  // authorize rule for any route
  isAuthorized: (state, getters, rootState) => (role) => {
    if (!role) {
      return true
    }
    return rootState.auth.user.role === role
  }
}

export const mutations = {
  SET_USER: (state, payload) => {
    state.user = payload
  },
  SET_USERS: (state, payload) => {
    state.users = payload
  }
}

export const actions = {
  getUser: ({ commit, rootState }, { id, axios, url }) => {
    if (id === rootState.auth.user._id) {
      commit('SET_USER', rootState.auth.user)
    } else {
      const { user } = axios.get(url)
      commit('SET_USER', user)
    }
  },

  // update tag
  updateUser: ({ dispatch, commit }, payload) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'user.profile', value: true },
        { root: true }
      )

      // if response user is the auth user then set the auth user
      if (payload.user._id === payload.auth.user._id) {
        payload.auth.setUser(payload.user)
      }
      commit('SET_USER', payload.user)
      commit(
        'loading/SET_LOADING',
        { option: 'user.profile', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'user.profile', value: false },
        { root: true }
      )
    }
  },

  // set user
  setUser: async ({ dispatch, commit }, payload) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'user.user', value: true },
        { root: true }
      )

      const response = await payload.axios.get('/api/users/' + payload.userId)
      // payload.auth.setUser(payload.user)
      commit('SET_USER', response.data.user)
      commit(
        'loading/SET_LOADING',
        { option: 'user.user', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'user.user', value: false },
        { root: true }
      )
    }
  }
}
