export const state = () => ({
  item: {},
  editItem: {},
  data: [],
  defaultItem: {},
  edit: false,
  dialog: false,
  confirm: false,
  valid: false,
  image: null,
  imageUrl: null,
  crudId: '',
  items: []
})

export const getters = {
  getDataItem: (state) => (name) => {
    const data = state.data.find((d) => d.name === name)
    return data.item
  }
}

export const mutations = {
  SET_ITEMS: (state, payload) => {
    state.items = payload
  },
  SET_DIALOG: (state, payload) => {
    state.dialog = payload
  },
  SET_CRUD_ID: (state, payload) => {
    state.crudId = payload
  },
  SET_CONFIRM: (state, payload) => {
    state.confirm = payload
  },
  SET_EDIT_ITEM: (state, payload) => {
    state.editItem[payload.name] = Object.assign({}, payload.item)
  },
  SET_ITEM: (state, payload) => {
    state.item = Object.assign({}, payload)
  },
  REMOVE_EDIT_ITEM: (state, payload) => {
    delete state.editItem[payload]
  },
  SET_DEFAULT_ITEM: (state, payload) => {
    state.defaultItem = Object.assign({}, payload)
  },
  SET_EDIT: (state, payload) => {
    state.edit = payload
  },
  SET_IMAGE_URL: (state, payload) => {
    state.imageUrl = payload
  }
}

export const actions = {
  // fetch items
  fetchItems: async (
    { commit },
    { axios, url, data = 'data', firstEmpty = false }
  ) => {
    try {
      if (firstEmpty) {
        commit('SET_ITEMS', [])
      }
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'data', value: true },
        { root: true }
      )
      const response = await axios.get(url)
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'data', value: false },
        { root: true }
      )
      commit('SET_ITEMS', response.data[data])
      return response.data[data]
    } catch (e) {
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'data', value: false },
        { root: true }
      )
    }
  },

  // add new item
  addItem: async (
    { commit, dispatch },
    { url, data, reload = false, axios, reloadUrl, fetchData = 'data' }
  ) => {
    try {
      // remove any alerts if exist
      commit('alert/REMOVE_ALERT', {}, { root: true })
      // set loading to true
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: true },
        { root: true }
      )
      // send api request
      const response = await axios.post(url, data)
      // set loading to false
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: false },
        { root: true }
      )
      // close dialog
      commit('crud/SET_DIALOG', false, { root: true })
      // display success message
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: 'crud-datatable',
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      if (reload) {
        dispatch('fetchItems', { url: reloadUrl, axios, data: fetchData })
      }
    } catch (e) {
      // set loading to false
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: false },
        { root: true }
      )
      // display error message
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: 'crud-dialog',
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )
    }
  },

  // edit item
  editItem: async (
    { commit, dispatch },
    { url, data, reload = false, axios, reloadUrl, fetchData = 'data' }
  ) => {
    try {
      // remove any alerts if exist
      commit('alert/REMOVE_ALERT', {}, { root: true })
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: true },
        { root: true }
      )
      const response = await axios.put(url, data)
      commit('crud/SET_DIALOG', false, { root: true })
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: false },
        { root: true }
      )
      // display success message
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: 'crud-datatable',
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      if (reload) {
        dispatch('fetchItems', { url: reloadUrl, axios, data: fetchData })
      }
    } catch (e) {
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'btn', value: false },
        { root: true }
      )
      // display error message
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: 'crud-dialog',
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )
    }
  },

  // deleteItem
  deleteItem: async (
    { commit, dispatch },
    { url, reload = false, axios, reloadUrl, fetchData = 'data' }
  ) => {
    try {
      // remove any alerts if exist
      commit('alert/REMOVE_ALERT', {}, { root: true })
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: true },
        { root: true }
      )

      const response = await axios.delete(url)
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: false },
        { root: true }
      )
      // display success message
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: 'crud-datatable',
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      // close confirm dialog
      commit('crud/SET_CONFIRM', false, { root: true })

      if (reload) {
        dispatch('fetchItems', { url: reloadUrl, axios, data: fetchData })
      }
    } catch (e) {
      // display error message
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: 'crud-confirm',
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )

      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: false },
        { root: true }
      )
    }
  },

  // deleteItem
  deleteMultipleItem: async (
    { commit, dispatch },
    { url, reload = false, axios, reloadUrl, fetchData = 'data', data = [] }
  ) => {
    try {
      // remove any alerts if exist
      commit('alert/REMOVE_ALERT', {}, { root: true })
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: true },
        { root: true }
      )

      const response = await axios.post(url, data)
      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: false },
        { root: true }
      )
      // display success message
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: 'crud-datatable',
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      // close confirm dialog
      commit('crud/SET_CONFIRM', false, { root: true })

      if (reload) {
        dispatch('fetchItems', { url: reloadUrl, axios, data: fetchData })
      }
    } catch (e) {
      // display error message
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: 'crud-confirm',
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )

      commit(
        'loading/SET_CRUD_LOADING',
        { option: 'delete', value: false },
        { root: true }
      )
    }
  }
}
