export const state = () => ({
  images: [],
  imageUrl: '/uploads'
})

export const getters = {
  getImage: (state) => (slug) => {
    const image = state.images.find((i) => i.slug === slug)
    return image ? '/uploads/images/' + image.image : ''
  }
}

export const mutations = {
  SET_IMAGES: (state, images) => {
    state.images = images
  }
}

export const actions = {
  // fetch all images
  fetchImages: async ({ commit }, { axios }) => {
    try {
      const response = await axios.get('/api/images')
      commit('SET_IMAGES', response.data.images)
    } catch (error) {}
  }
}
