<template>
  <v-menu
    ref="menu"
    v-model="menu"
    :close-on-content-click="false"
    transition="scale-transition"
    offset-y
    min-width="290px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-text-field
        v-model="date"
        label="Birthday date"
        prepend-inner-icon="mdi-calendar"
        readonly
        outlined
        dense
        single-line
        v-bind="attrs"
        v-on="on"
      ></v-text-field>
    </template>
    <v-date-picker
      ref="picker"
      v-model="date"
      :max="new Date().toISOString().substr(0, 10)"
      min="1950-01-01"
      @change="save(date)"
    ></v-date-picker>
  </v-menu>
</template>

<script>
import moment from 'moment'
export default {
  props: {
    value: {
      type: [String, Boolean, Date],
      default: null
    }
  },
  data: (vm) => ({
    date: moment(vm.value).format('YYYY-MM-DD'),
    menu: false
  }),
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
    value(to, from) {
      if (to) {
        this.date = moment(to).format('YYYY-MM-DD')
      }
    },
    date(to, from) {
      this.$emit('input', this.date)
    }
  },
  methods: {
    save(date) {
      this.$refs.menu.save(date)
      this.$emit('input', this.date)
    }
  }
}
</script>
