export const state = () => ({
  roles: []
})

export const mutations = {
  SET_ROLES: (state, payload) => {
    state.roles = payload
  }
}

export const getters = {
  getRole: (state) => (slug) => {
    const role = state.roles.find((role) => role.slug === slug)
    if (role) {
      return role
    } else {
      return null
    }
  }
}

export const actions = {
  fetchRoles: async ({ commit }, { axios }) => {
    try {
      const response = await axios.get('/api/roles')
      commit('SET_ROLES', response.data.roles)
    } catch (error) {}
  }
}
