export const state = () => ({
  type: '',
  color: '',
  text: '',
  snack: false,
  timeout: 5000
})

export const mutations = {
  SET_SNACK: (state, payload) => {
    state.type = payload.type ? payload.type : ''
    state.timeout = payload.timeout ? payload.timeout : 5000
    state.color = payload.color ? payload.color : ''
    state.text = payload.text ? payload.text : ''
    state.snack = payload.snack ? payload.snack : true
  },
  REMOVE_SNACK: (state) => {
    state.snack = false
    state.type = ''
    state.text = ''
    state.color = ''
    state.timeout = 5000
  }
}
