export const state = () => ({
  settings: {
    app: {
      app: {
        title: 'Brand',
        favicon: '',
        logo: 'logo.jpg'
      },
      smtp: {
        host: null,
        username: null,
        email: null,
        password: null,
        name: null,
        port: null,
        encryption: ''
      }
    },
    blog: {
      reading: {
        postLimit: 5,
        featurePostLimit: 3
      },
      layout: {
        postCols: 1,
        sidebar: true,
        header: true
      },
      comments: {
        allow: true,
        commentsLimit: 5,
        repliesLimit: 5
      }
    },
    theme: {
      colors: {
        primary: '#037C42',
        success: '#2FAA1C',
        secondary: '#81811E',
        info: '#55A3F3',
        error: '#E65527',
        warning: '#FFFFFF',
        anchor: '#2F85A0'
      },
      theme: {
        dark: true
      }
    }
  }
})

export const mutations = {
  SET_SETTINGS: (state, payload) => {
    state.settings = payload
  },
  SET_VUETIFY_COLORS: (state, vuetify) => {
    for (const a in vuetify.theme.themes.light) {
      if (vuetify.theme.themes.light[a] && state.settings.theme.colors[a]) {
        if (state.settings.theme.colors[a].includes('#')) {
          vuetify.theme.themes.light[a] = state.settings.theme.colors[a]
        } else {
          vuetify.theme.themes.light[a] =
            state.settings.theme.colors[state.settings.theme.colors[a]]
        }
      }
    }

    for (const a in vuetify.theme.themes.dark) {
      if (vuetify.theme.themes.dark[a] && state.settings.theme.colors[a]) {
        if (state.settings.theme.colors[a].includes('#')) {
          vuetify.theme.themes.dark[a] = state.settings.theme.colors[a]
        } else {
          vuetify.theme.themes.dark[a] =
            state.settings.theme.colors[state.settings.theme.colors[a]]
        }
      }
    }
  },
  SET_THEME: (state, vuetify) => {
    vuetify.theme.dark = state.settings.theme.theme.dark
  }
}

export const actions = {
  // fetch all settings
  fetchSettings: async ({ state, commit }, { axios, vuetify }) => {
    try {
      const response = await axios.get('/api/settings')
      commit('SET_SETTINGS', response.data.settings)
      commit('SET_THEME', vuetify)
      commit('SET_VUETIFY_COLORS', vuetify)
    } catch (error) {}
  }
}
