export const state = () => ({
  showDeleteDialog: false
})

export const mutations = {
  SET_DELETE_DIALOG: (state, payload) => {
    state.showDeleteDialog = payload
  }
}

export const actions = {
  // add new comment
  addComment: async ({ dispatch }, { comment, axios }) => {
    await axios.post('/api/comments', comment)
    await dispatch(
      'post/fetchPost',
      { slug: comment.slug, axios, loading: false },
      { root: true }
    )
  },

  // delete comment
  deleteComment: async ({ dispatch, commit, state }, { commentId, axios }) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'comments.delete', value: true },
        { root: true }
      )
      await axios.delete(`/api/comments/`)
      commit(
        'loading/SET_LOADING',
        { option: 'comments.delete', value: false },
        { root: true }
      )
      state.showDeleteDialog = false
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'comments.delete', value: false },
        { root: true }
      )
      state.showDeleteDialog = false
    }
  }
}
