import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

export default async (ctx) => {
  let locale
  if (process.browser) {
    locale = localStorage.getItem('lang')
  }
  // Set i18n instance on app
  // This way we can use it in middleware and pages asyncData/fetch
  await ctx.store.dispatch('lang/fetchLangs', ctx.$axios)

  const locales = ctx.store.getters['lang/locales']
  if (locale) {
    if (!locales.includes(locale)) {
      locale = ctx.store.state.lang.fallbackLocale
    }
    ctx.store.commit('lang/SET_LANG', locale)
  }
  await ctx.store.dispatch('lang/fetchTranslation', {
    axios: ctx.$axios,
    locale: ctx.store.state.lang.locale
  })
  ctx.app.i18n = new VueI18n({
    locale: ctx.store.state.lang.locale,
    fallbackLocale: 'en',
    messages: ctx.store.state.lang.translations
  })

  ctx.app.i18n.path = (link) => {
    return `${link}`
  }
}
