export const state = () => ({
  notifications: []
})

export const getters = {
  unreadNotificationsLength(state) {
    const nots = state.notifications.filter((not) => !not.read)
    return String(nots.length)
  },
  dropdownNotifications(state) {
    const nots = state.notifications
    return nots.slice(0, 5)
  }
}

export const mutations = {
  SET_NOTIFICATIONS: (state, payload) => {
    state.notifications = payload
  }
}

export const actions = {
  // fetch all notifications
  fetchNotifications: async ({ commit, rootState }, axios) => {
    try {
      const response = await axios.get(
        '/api/notifications/' + rootState.auth.user._id,
        axios,
        commit
      )
      commit('SET_NOTIFICATIONS', response.data.notifications)
    } catch (error) {}
  },

  // read notifications
  readNotifications: async ({ commit, getters, rootState }, axios) => {
    try {
      if (getters.unreadNotificationsLength > 0) {
        const response = await axios.put(
          '/api/notifications/' + rootState.auth.user._id
        )
        commit('SET_NOTIFICATIONS', response.data.notifications)
      }
    } catch (error) {}
  }
}
