export const state = () => ({
  type: '',
  text: '',
  show: false,
  display: ''
})

export const mutations = {
  SET_ALERT: (state, payload) => {
    state.type = payload.type ? payload.type : ''
    state.text = payload.text ? payload.text : ''
    state.show = payload.show ? payload.show : false
    state.display = payload.display ? payload.display : ''
  },
  REMOVE_ALERT: (state, payload = false) => {
    state.type = ''
    state.text = ''
    state.show = payload
    state.display = ''
  }
}
