export const state = () => ({
  subscribers: [],
  count: 0
})

export const mutations = {
  SET_COUNT: (state, payload) => {
    state.count = payload
  },
  SET_SUBSCRIBERS: (state, payload) => {
    state.subscribers = payload
  }
}

export const actions = {
  // add new subscriber
  addSubscriber: async (
    { commit },
    { subscriber, axios, form, alertDisplay }
  ) => {
    try {
      commit('alert/REMOVE_ALERT', {}, { root: true })
      commit(
        'loading/SET_LOADING',
        { option: 'newsletter', value: true },
        { root: true }
      )
      const response = await axios.post('/api/newsletter', subscriber)
      commit(
        'loading/SET_LOADING',
        { option: 'newsletter', value: false },
        { root: true }
      )
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: alertDisplay,
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      // reset form validation
      form.resetValidation()
      // reset the form
      form.reset()
    } catch (e) {
      commit(
        'loading/SET_LOADING',
        { option: 'newsletter', value: false },
        { root: true }
      )
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: alertDisplay,
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )
    }
  }
}
