<template>
  <div v-if="alert.display === display">
    <v-alert
      v-model="alert.show"
      :type="alert.type"
      dismissible
      close-icon="mdi-delete"
      transition="scale-transition"
    >
      {{ $t(alert.text) }}
    </v-alert>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  props: {
    display: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapState(['alert'])
  }
}
</script>

