<template>
  <div :style="styles" v-bind="$attrs" :class="classes"></div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: {
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '3px'
    },
    color: {
      type: String,
      default: 'primary'
    },
    center: {
      type: Boolean,
      default: false
    },
    right: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classes() {
      return [
        this.center ? 'mx-auto ' : '',
        this.right ? 'ml-auto ' : '',
        this.color
      ]
    },
    styles() {
      return {
        width: this.width,
        height: this.height,
        backgroundColor: this.color
      }
    }
  }
}
</script>

<style lang="scss" scoped></style>
