import defaultTranslations from '@/localization/translations.json'

export const state = () => ({
  langs: [{ name: 'English', locale: 'en' }],
  translations: defaultTranslations,
  locale: 'en',
  fallbackLocale: 'en',
  count: 0,
  localLang: null
})

export const getters = {
  locales(state) {
    return state.langs.map((lang) => lang.locale)
  }
}

export const mutations = {
  SET_LANG(state, locale) {
    // if (state.locales.includes(locale)) {
    state.locale = locale
    // }
  },
  SET_LANGS(state, payload) {
    state.langs = payload
  },
  SET_TRANSLATIONS(state, payload) {
    state.translations = payload
  }
}

export const actions = {
  // fetch all langs
  fetchLangs: async (context, axios) => {
    try {
      const response = await axios.get('/api/langs')
      context.commit('SET_LANGS', response.data.languages)
    } catch (error) {}
  },

  // get translations
  fetchTranslations: async (context, { axios, locale }) => {
    try {
      const response = await axios.get(`/api/langs/translations/${locale}`)
      context.commit('SET_TRANSLATIONS', response.data.translation)
    } catch (error) {}
  },

  // get translation
  fetchTranslation: async (context, { axios, locale, i18n = false }) => {
    try {
      const response = await axios.get(`/api/langs/translation/${locale}`)
      context.commit('SET_TRANSLATIONS', response.data.translation)
      if (i18n) {
        i18n.setLocaleMessage(state.translations)
      }
    } catch (error) {}
  }
}
