export const actions = {
  // add new inquiry
  addInquiry: async ({ commit }, { inquiry, axios, form }) => {
    try {
      commit('alert/REMOVE_ALERT', {}, { root: true })
      commit(
        'loading/SET_LOADING',
        { option: 'inquiry', value: true },
        { root: true }
      )
      const response = await axios.post('/api/inquiries', inquiry)
      commit(
        'loading/SET_LOADING',
        { option: 'inquiry', value: false },
        { root: true }
      )
      commit(
        'alert/SET_ALERT',
        {
          type: 'success',
          display: 'app-contact',
          show: true,
          text: response.data.message ? response.data.message : response.message
        },
        { root: true }
      )
      // reset form validation
      form.resetValidation()
      // reset the form
      form.reset()
    } catch (e) {
      commit(
        'loading/SET_LOADING',
        { option: 'inquiry', value: false },
        { root: true }
      )
      commit(
        'alert/SET_ALERT',
        {
          type: 'error',
          display: 'app-contact',
          show: true,
          text: e.response.data.message
            ? e.response.data.message
            : e.response.message
        },
        { root: true }
      )
    }
  }
}
