export const state = () => ({
  menus: []
})

export const getters = {
  getMenu: (state, getters) => (slug, auth) => {
    const menu = state.menus.find((menu) => menu.slug === slug)
    // if menu not found
    if (!menu) {
      return []
    }
    // check if user loggedin
    if (auth.loggedIn) {
      let items = menu.items.filter((item) => {
        if (item.loggedIn && item.loggedOut) {
          return true
        } else if (item.loggedIn) {
          return true
        } else {
          return false
        }
      })
      items = items.filter((item) => {
        if (!item.permission) {
          return true
        } else if (item.permission.length === 0) {
          return true
        } else if (item.permission.includes(auth.user.role.title)) {
          return true
        } else {
          return false
        }
      })

      items = items.filter((item) => {
        item.children.forEach((element, index) => {
          if (element.loggedIn && element.loggedOut) {
            // return true
          } else if (element.loggedIn) {
            // return true
          } else {
            item.children.splice(index, 1)
          }
        })
        return true
      })

      items = items.filter((item) => {
        item.children.forEach((element, index) => {
          if (!element.permission) {
            // return true
          } else if (element.permission.length === 0) {
            // return true
          } else if (
            element.permission.includes(
              auth.user.role ? auth.user.role.title : 'user'
            )
          ) {
            // return true
          } else {
            item.children.splice(index, 1)
          }
        })
        return true
      })
      return items
    } else {
      let items = menu.items.filter((item) => {
        if (item.loggedIn && item.loggedOut) {
          return true
        } else if (item.loggedOut) {
          return true
        } else {
          return false
        }
      })

      items = items.filter((item) => {
        item.children.forEach((element, index) => {
          if (element.loggedIn && element.loggedOut) {
            // return true
          } else if (element.loggedIn) {
            // return true
          } else {
            item.children.splice(index, 1)
          }
        })
        return true
      })

      return items
    }
  },

  getChildrens: (state) => (children) => {
    const childs = children.filter((ch) => {
      return true
    })
    return childs
  }
}

export const mutations = {
  SET_MENUS: (state, payload) => {
    state.menus = payload
  }
}

export const actions = {
  fetchMenus: async ({ commit }, { axios }) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'menu.menus', value: true },
        { root: true }
      )
      const response = await axios.get('/api/menus')
      commit('SET_MENUS', response.data.menus)
      commit(
        'loading/SET_LOADING',
        { option: 'menu.menus', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'menu.menus', value: false },
        { root: true }
      )
    }
  }
}
