export const state = () => ({
  posts: [],
  featuredPosts: [],
  categories: [],
  tags: [],
  count: 0,
  page: 1,
  post: null
})

export const mutations = {
  SET_COUNT: (state, payload) => {
    state.count = payload
  },
  SET_POSTS: (state, payload) => {
    state.posts = payload
  },
  SET_FEATURED_POSTS: (state, payload) => {
    state.featuredPosts = payload
  },
  SET_POST: (state, payload) => {
    state.post = payload
  },
  UPDATE_POST_VIEW: (state, payload) => {
    state.post.views += payload
  },
  SET_CATEGORIES: (state, payload) => {
    state.categories = payload
  },
  SET_TAGS: (state, payload) => {
    state.tags = payload
  }
}

export const actions = {
  // fetch all posts
  fetchPosts: async ({ state, commit }, { axios, query = '' }) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.posts', value: true },
        { root: true }
      )
      const response = await axios.get('/api/posts?page=' + state.page + query)
      commit('SET_POSTS', response.data.posts)
      commit('SET_COUNT', response.data.count)
      commit(
        'loading/SET_LOADING',
        { option: 'blog.posts', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.posts', value: false },
        { root: true }
      )
    }
  },

  // fetch featured posts
  fetchFeaturedPosts: async ({ commit, rootState }, { axios, query = '' }) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.featuredPosts', value: true },
        { root: true }
      )
      const response = await axios.get(
        '/api/posts?featured=true&status=published&limit=' +
          rootState.settings.settings.blog.reading.featurePostLimit +
          '' +
          query
      )
      commit('SET_FEATURED_POSTS', response.data.posts)
      commit(
        'loading/SET_LOADING',
        { option: 'blog.featuredPosts', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.featuredPosts', value: false },
        { root: true }
      )
    }
  },

  // fetch single post
  fetchPost: async (
    { dispatch, commit, state },
    { slug, filter = false, axios, updateView = false, loading = true }
  ) => {
    try {
      if (loading) {
        commit(
          'loading/SET_LOADING',
          { option: 'blog.post', value: true },
          { root: true }
        )
      }
      const post = state.posts.find((post) => post.slug === slug)
      if (post && filter) {
        commit('SET_POST', post)
        if (updateView) {
          commit('UPDATE_POST_VIEW', 1)
          dispatch('updatePostView', {
            postSlug: state.post.slug,
            axios
          })
        }
      } else {
        const response = await axios.get(
          '/api/posts/' + slug + '?populate=true'
        )
        commit('SET_POST', response.data.post)
        if (updateView && response.data.post) {
          commit('UPDATE_POST_VIEW', 1)
          dispatch('updatePostView', {
            postSlug: state.post.slug,
            axios
          })
        }
      }
      if (loading) {
        commit(
          'loading/SET_LOADING',
          { option: 'blog.post', value: false },
          { root: true }
        )
      }
    } catch (error) {
      if (loading) {
        commit(
          'loading/SET_LOADING',
          { option: 'blog.post', value: false },
          { root: true }
        )
      }
    }
  },

  // update post view
  updatePostView: async ({ commit }, { postSlug, axios }) => {
    try {
      await axios.put(`/api/posts/updateView/${postSlug}`)
    } catch (error) {}
  },

  // fetch all categories
  fetchCategories: async ({ commit }, axios) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.categories', value: true },
        { root: true }
      )
      const response = await axios.get('/api/categories')
      commit('SET_CATEGORIES', response.data.categories)
      commit(
        'loading/SET_LOADING',
        { option: 'blog.categories', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.categories', value: false },
        { root: true }
      )
    }
  },

  // fetch all tags
  fetchTags: async ({ commit }, axios) => {
    try {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.tags', value: true },
        { root: true }
      )
      const response = await axios.get('/api/tags')
      commit('SET_TAGS', response.data.tags)
      commit(
        'loading/SET_LOADING',
        { option: 'blog.tags', value: false },
        { root: true }
      )
    } catch (error) {
      commit(
        'loading/SET_LOADING',
        { option: 'blog.tags', value: false },
        { root: true }
      )
    }
  }
}
