<template>
  <div>
    <v-img
      v-if="getImage(image)"
      :width="width ? width : ''"
      :height="height ? height : ''"
      class="mb-3"
      :src="getImage(image)"
      :aspect-ratio="aspectRatio ? aspectRatio : ''"
    ></v-img>
    <div class="cursor-pointer">
      <v-file-input
        v-if="typeof image === 'object' && image !== null"
        :label="label"
        :value="image"
        outlined
        :clearable="false"
        :rules="rules ? rules : []"
        :dense="dense"
        @change="setImage"
      >
        <template slot="append">
          <v-icon color="primary" @click="defaultImage">mdi-close</v-icon>
        </template>
      </v-file-input>
      <v-text-field
        v-else
        outlined
        readonly
        :label="label"
        :rules="rules ? rules : []"
        :value="image"
        :dense="dense"
        @click="openUploader"
      ></v-text-field>
    </div>
    <div style="visibility:hidden; position:absolute">
      <v-file-input
        :id="id ? id : 'uploader'"
        :dense="dense"
        @change="setImage"
      />
    </div>
    <v-btn
      v-if="removeButton"
      :color="removeButton.color ? removeButton.color : 'error'"
      :disabled="!prevImage"
      @click="removeImage"
    >
      {{ removeButton.text ? removeButton.text : 'Remove Image' }}
    </v-btn>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line
  props: ['value', 'imagePath', 'width', 'removeButton', 'label', 'id', 'aspectRatio', 'height', 'rules', 'dense'],
  data() {
    return {
      image: this.value,
      url: '',
      prevImage: this.value
    }
  },
  watch: {
    value(to, from) {
      this.image = to
    }
  },
  methods: {
    getImage(image) {
      if (image) {
        if (typeof image === 'string') {
          if (!image.startsWith('data:image')) {
            return this.imagePath + image
          } else {
            return image
          }
        } else {
          const fileReader = new FileReader()
          fileReader.addEventListener('load', () => {
            this.url = fileReader.result
          })
          fileReader.readAsDataURL(image)
          return this.url
        }
      } else if (this.prevImage) {
        return this.imagePath + this.prevImage
      } else {
        return this.prevImage
      }
    },
    setImage(value) {
      this.image = value
      this.$emit('input', this.image)
    },
    defaultImage() {
      this.image = this.prevImage
      this.$emit('input', this.image)
    },
    removeImage() {
      this.image = null
      this.prevImage = null
      this.$emit('input', null)
    },
    openUploader() {
      document.getElementById(this.id ? this.id : 'uploader').click()
    }
  }
}
</script>

<style lang="scss" scoped>
.cursor-pointer {
  cursor: pointer;
}
</style>
