import {
  postData,
  getData,
  deleteData,
  putData
} from '../utils/datatableApiRequests'

export const state = () => ({
  modules: [],
  count: 0
})

export const mutations = {
  SET_COUNT: (state, payload) => {
    state.count = payload
  },
  SET_MODULES: (state, payload) => {
    state.modules = payload
  }
}

export const actions = {
  // fetch all modules
  fetchModules: async ({ commit }, axios) => {
    const { modules, count } = await getData('/api/modules', axios, commit)
    commit('SET_MODULES', modules)
    commit('SET_COUNT', count)
  },

  // add new module
  addModule: async ({ dispatch, commit }, { module, axios }) => {
    await postData('/api/modules', module, axios, commit)
    await dispatch('fetchModules', axios)
  },

  // update module
  updateModule: async ({ dispatch, commit }, { module, axios }) => {
    await putData(`/api/modules/${module._id}`, module, axios, commit)
    await dispatch('fetchModules', axios)
  },

  // delete module
  deleteModule: async ({ dispatch, commit }, { moduleId, axios }) => {
    await deleteData(`/api/modules/${moduleId}`, axios, commit)
    await dispatch('fetchModules', axios)
  }
}
