export const state = () => ({
  data: false,
  btn: false,
  delete: false,
  loader: true,
  inquiry: false,
  newsletter: false,
  page: false,
  menu: {
    menus: false
  },
  user: {
    profile: false,
    user: false
  },
  crud: {
    fetch: false,
    add: false,
    edit: false,
    data: false,
    btn: false,
    delete: false
  },
  blog: {
    post: false,
    posts: false,
    featuredPosts: false,
    categories: false,
    tags: false
  },
  comments: {
    add: false,
    delete: false
  },
  replies: {
    add: false,
    delete: false
  },
  auth: {
    login: false,
    register: false,
    forgotPassword: false,
    resetPassword: false
  }
})

export const mutations = {
  SET_LOADING: (state, { option, value }) => {
    if (option.includes('.')) {
      const arr = option.split('.', 2)
      const o1 = arr[0]
      const o2 = arr[1]
      state[o1][o2] = value
    } else {
      state[option] = value
    }
  },
  SET_BLOG_LOADING: (state, { option, value }) => {
    state.blog[option] = value
  },
  SET_CRUD_LOADING: (state, { option, value }) => {
    state.crud[option] = value
  },
  SET_AUTH_LOADING: (state, { option, value }) => {
    state.auth[option] = value
  }
}
